//
$primary: #3F72AF;
$secondary: #DBE2EF;
$auxiliary: #112D4E;

//
@mixin bg-dark {
    background-color: rgba(0, 0, 0, .6);
    background-blend-mode: multiply;
}

.bg-blend-mode {
    background-color: rgba(0, 0, 0, .6);
    background-blend-mode: multiply;
}

//
a {
    text-decoration: none;
}

//
.bg-cover {
    background-size: cover;
    background-position: center center;
}

.overflow-x-hidden {
    overflow-x: hidden;
}

.overflow-y-hidden {
    overflow-y: hidden
}


//
.rounded-lg {
    border-radius: 16px;
}

.rounded-md {
    border-radius: 4.8px;
}

.rounded-sm {
    border-radius: 4px;
}

//
.parallax-img {
    background-attachment: fixed;
}

//
.bg--translucent {
    background-color: rgba(0, 0, 0, 0.5);
}

.display-none {
    display: none;
}